.contacts-form {
	width: 100%;
	margin-top: 50px;

	&_title {
		@include font(24px, 28px, #2A4045, 400);
		margin-bottom: 16px;
		font-family: 'Roboto', sans-serif;
	}

	&_form {
		width: 100%;
		margin: 0;
		border: 0;
		border-radius: 3px;
		background: #fff;
		padding: 46px 41px 44px 40px;
		display: flex;
		flex-direction: column;
		font-family: 'Roboto', sans-serif;
		transition: all 0.3s ease 0s;
	}

	&_accordion {
		transform: translateY(-200%);
		position: absolute;
		z-index: -1;
		opacity: 0;
		top: 0;
		left: 0;
		transition: all 0.3s ease 0s;
		display: flex;
		margin-top: 10px;
		margin-bottom: 20px;

		&-checkbox {
			position: absolute;
			z-index: -1;
			opacity: 0;
			visibility: hidden;
			left:	0;
			top: 0;

			&:checked + .contacts-form_accordion {
				transform: translateY(0%);
				position: static;
				opacity: 1;
				transition: all 0.3s ease 0s;
			}
		}
	}

	&_label {
		@include font(16px, 19px, rgba(0, 0, 0, 0.6), 400);
		cursor: pointer;
		font-family: 'Roboto', sans-serif;

		&:not(:first-child) {
			margin-top: 40px;
		}

		& > span {
			display: block;
			grid-row: 1;
			grid-column: 1 / -1;
		}

		&-address {
			display: grid;
			grid-template-columns: auto repeat(2, 98px);
			row-gap: 10px;
			column-gap: 14px;
		}

		&-data {
			display: grid;
			grid-template-columns: auto 240px;
			row-gap: 10px;
			column-gap: 14px;
		}

		&-accordion {
			overflow: hidden;
			position: relative;

			& > span {
				display: inline-flex;
				align-items: center;
				justify-content: space-between;
				column-gap: 10px;
				background: #ffffff;
				position: relative;
				z-index: 1;

				svg {
					fill: none;
					stroke: #31494E;
					stroke-width: 2px;
					stroke-linecap: round;
				}
			}
		}
	}

	&_input {
		@include font(16px, 19px, rgba(0, 0, 0, 0.6), 400);
		font-family: 'Roboto', sans-serif;
		border: 1px solid rgba(0, 0, 0, 0.1);
		opacity: 1;
		padding: 18px 20px;
		border-radius: 3px;
		outline: none;
		-moz-appearance: textfield;

		&::placeholder {
			@include font(16px, 19px, rgba(0, 0, 0, 0.6), 400);
			opacity: 1;
		}

		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}

		&:hover,
		&:focus {
			-moz-appearance: number-input;
		}

		&-promo {
			width: 100%;
			padding-right: 99px;
		}
	}

	&_promo {
		&-button {
			background: rgba(42, 64, 69, 1);
			display: block;
			width: 99px;
			height: 100%;
			border-radius: 3px;
			position: absolute;
			right: 0;
			bottom: 0;
			color: #ffffff;
		}
	}

	&_term {
		&-label {
			margin-top: 20px;
			display: inline-flex;
			align-items: flex-start;
			column-gap: 15px;
			cursor: pointer;
		}

		&-input {
			position: absolute;
			z-index: -1;
			opacity: 0;

			&:checked + .contacts-form_term-content:before {
				background-image: url("data:image/svg+xml;charset=utf8, %3Csvg width='18' height='14' viewBox='0 0 18 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 6.154L7.008 11 16 2' stroke='%238DC63F' stroke-width='3'/%3E%3C/svg%3E");
				background-position: 50% 50%;
				background-repeat: no-repeat;
			}
		}

		&-content {
			@include font(16px, 150%, rgba(0, 0, 0, 0.6), 400);
			width: 100%;
			max-width: 566px;
			display: inline-flex;
			column-gap: 15px;
			align-items: flex-start;

			&::before {
				content: '';
				display: inline-block;
				width: 20px;
				height: 20px;
				border: 1px solid rgba(247, 247, 247, 1);
				border-radius: 2px;
				flex-shrink: 0;
				flex-grow: 0;
				background-color: rgba(254, 254, 254, 1);
			}
		}

		&-link {
			@include font(16px, 150%, rgba(0, 0, 0, 0.6), 400);
			display: inline;
			text-decoration: none;
			border-bottom: 1px solid rgba(0, 0, 0, 0.2);
		}
	}

	&_submit {
		&-button {
			max-width: 240px;
			margin-top: 30px;
		}
	}
}

@media screen and (max-width: 768px) {
	.contacts-form {
		&_form {
			padding: 20px 10px;
		}
	}
}

@media screen and (max-width: 576px) {
	.contacts-form {
		margin-top: 30px;

		&_title {
			font-size: 16px;
			line-height: 19px;
		}

		&_label {
			font-size: 10px;
			line-height: 12px;

			&:not(:first-child) {
				margin-top: 20px;
			}

			&-address {
				grid-template-columns: repeat(2, 1fr);
				column-gap: 20px;
				row-gap: 10px;
			}

			&-data {
				grid-template-columns: 100%;
			}

			&-accordion {
				svg {
					width: 8px;
				}
			}
		}

		&_input {
			padding: 11px 12px;

			&::placeholder {
				font-size: 13px;
				line-height: 17px;
			}

			&-address {
				grid-column: 1/-1;
			}

			&-promo {
				padding-right: 99px;
			}
		}

		&_promo {
			&-button {
				font-size: 12px;
				line-height: 14px;
				width: 99px;
			}
		}

		&_term {
			&-content {
				font-size: 10px;
				line-height: 15px;
			}

			&-link {
				font-size: 10px;
				line-height: 15px;
			}
		}

		&_submit-button {
			width: 100%;
			max-width: 100%;
		}
	}
}
