.terms {
	width: 100%;
	max-width: 973px;
	background: #ffffff;
	padding: 20px 30px;
	margin: 107px auto 0;

	&_title {
		@include font(16px, 28px, rgba(0,0,0,0.7), 400);
		display: flex;
		justify-content: space-between;
		align-items: center;
		column-gap: 8px;
		cursor: pointer;
		margin-bottom: 0;
		transition: all 0.3s ease 0s;

		&.show {
			margin-bottom: 20px;
			transition: all 0.3s ease 0s;

			svg {
				transform: rotate(-180deg);
				transition: all 0.3s ease 0s;
			}
		}

		svg {
			display: inline-block;
			vertical-align: middle;
			stroke-width: 2px;
			stroke-linecap: round;
			stroke: #31494e;
			fill: none;
			transform: rotate(0deg);
			transition: all 0.3s ease 0s;
		}
	}

	&_list {
		height: 0;
		overflow: hidden;
		transition: all 0.3s ease 0s;
		display: flex;
		flex-direction: column;
		row-gap: 10px;
	}

	&_item {
		width: 100%;
		cursor: pointer;

		&-title {
			@include font(14px, 21px, #206DC9, 400);
			border-bottom-width: 1px;
			border-bottom-style: dashed;
			border-color: #B09090;
			cursor: pointer;
			width: fit-content;
		}
	}

	&_sub {
		&-list {
			list-style: inside;
			height: 0;
			overflow: hidden;
			padding-top: 0;
			transition: all 0.3s ease 0s;
		}

		&-item {
			&::marker {
				color: #8DC63F;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.terms {
		padding: 10px 20px;

		&_title {
			font-size: 14px;
			line-height: 28px;
		}
	}
}
