.button {
	cursor: pointer;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	text-align: center;
	border-radius: 3px;

	&_size_regular {
		padding: 11px 33px;
		font: 400 15px 'Roboto';
		letter-spacing: 0.5px;
	}

	&_size_medium {
		padding: 16px 64px;
		font: 400 16px 'Roboto';
		letter-spacing: 0.5px;
	}

	&_theme_green-border {
		border: 2px solid #8DC63F;
		background: transparent;
		color: black;
		transition: background 0.2s ease-in-out;

		&:hover {
			background: rgba(#8DC63F, 0.1);
		}
	}

	&_theme_full-orange {
		border: 0;
		background: #FF9100;
		color: white;
		transition: background 0.2s ease-in-out;
		&:hover {
			background: darken(#FF9100, 5%);
		}
	}

	&_theme_full-green {
		border: 0;
		background: #8DC63F;
		color: white;
		transition: background 0.2s ease-in-out;

		&:hover {
			background: darken(#8DC63F, 5%);
		}
	}

	&__text + &__icon {
		margin-left: 25px;
	}


	&__icon + &__text {
		margin-left: 25px;
	}


	&__text {
		flex-shrink: 1;
		text-align: center;
		margin: 0 auto;
	}

	&__icon {
		flex-shrink: 0;
	}

}
