.recommendations {
	width: 100%;
	margin-top: 50px;

	&_title {
		@include font(24px, 28px, #2A4045, 400);
		margin-bottom: 16px;
		font-family: 'Roboto', sans-serif;
	}

	&_cards {
		list-style: none;
		padding: 0;
		margin: 0;
		display: flex;
		flex-direction: column;
		row-gap: 20px;
	}

	&_card {
		font-family: 'Roboto', sans-serif;
		width: 100%;
		background: #fff;
		border-radius: 3px;
		padding: 15px 28px 15px 15px;
		display: grid;
		grid-template-columns: minmax(0, 302px) 152px auto;
		justify-content: space-between;
		align-items: center;
		column-gap: 20px;

		&.active {
			background: #80b636;
			transition: all 0.3s;

			.button {
				background: #80b636;

				&_theme_full-orange {
					background: #e68300;
				}
			}

			.recommendations_card {
				&-caption {
					color: $white;
					transition: all 0.3s;
				}

				&-tariff {
					&-description {
						color: $white;
						transition: all 0.3s;
					}

					&-features {
						color: $white;
						transition: all 0.3s;
					}
				}

				&-price {
					&-month {
						color: $white;
						transition: all 0.3s;
					}
				}

				&-advantage {
					background: $white;
					color: #80b636;
					transition: all 0.3s;
				}
			}

			.button_theme_full-green {
				background: $white;
				color: #80b636;
				transition: all 0.3s;
			}
		}

		&-orange.active {
			background: #ff9100;

			.recommendations_card {
				&-advantage {
					&-orange {
						background: $white;
						color: #ff9100;

						span {
							color: #ff9100;
						}
					}
				}

				&-tariff {
					&-orange {
						color: $white;
					}

					&-description {
						color: #000;
					}

					&-features {

					}
				}

				&-price {
					&-month {
						color: #000;
					}
				}
			}

			.button_theme_full-orange {
				background: $white;
				color: #ff9100;
			}
		}

		.button {
			padding: 18px 8px;
			width: 114px;
		}

		&-info {
			display: grid;
			grid-template-columns: 81px minmax(0, 202px);
			column-gap: 29px;
		}

		&-advantage {
			@include font(28px, 33px, $white, 400);
			grid-row: 1 / -4;
			background: #8dc63f;
			border-top-left-radius: 50%;
			border-top-right-radius: 50%;
			border-bottom-right-radius: 50%;
			width: 81px;
			height: 81px;
			display: flex;
			justify-content: center;
			align-items: center;
			transition: all 0.3s;

			&-orange {
				background: #FF9100;
				flex-direction: column;

				span {
					@include font(14px, 18px, #fff, 400);
				}
			}
		}

		&-caption {
			@include font(14px, 21px, #000, 600);
			transition: all 0.3s;
		}

		&-tariff {
			@include font(16px, 25px, #377C2B, 500);
			text-transform: uppercase;
			transition: all 0.3s;

			&-orange {
				color: #FF9100;
			}

			&-description {
				@include font(14px, 21px, #000, 400);
				display: block;
				margin-top: 4px;
				transition: all 0.3s;

				&-cinema {
					@include font(14px, 21px, #000, 400);
					display: block;
				}
			}

			&-features {
				position: relative;
			}

			&-feature {
				border-bottom-width: 1px;
				border-bottom-style: dashed;
				border-color: #b09090;
				cursor: pointer;
				width: fit-content;

				&.show-tip:hover + .recommendations_card-tariff-feature-tip {
					opacity: 1;
					transform: scale(1);
					transition: all 0.3s ease 0s;
				}

				&-tip {
					@include font(14px, 21px, rgba(0, 0, 0, 0.5), 400);
					margin: 0;
					opacity: 0;
					transform: scale(0);
					position: absolute;
					top: 100%;
					left: 0;
					background: #ffffff;
					padding: 35px;
					box-shadow: 0 5px 34px rgba(0, 0, 0, 0.1);
					border-radius: 3px;
					transform-origin: 0 -20px;
					transition: all 0.3s ease 0s;

					&:hover {
						opacity: 1;
						transform: scale(1);
						transition: all 0.3s ease 0s;
					}
				}
			}
		}

		&-price {
			display: flex;
			flex-direction: column;
			transition: all 0.3s;

			&-advantage {
				@include font(14px, 21px, rgba(55, 124, 43, 1), 400);
				transition: all 0.3s;
			}

			&-month {
				@include font(32px, 38px, #000, 400);
				white-space: nowrap;
				transition: all 0.3s;
			}

			&-total {
				@include font(14px, 21px, rgba(55, 124, 43, 1), 400);
				transition: all 0.3s;
			}

			&-more {
				@include font(14px, 18px, #000, 400);
				transition: all 0.3s;
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.recommendations {
		&_card {
			column-gap: 10px;
			padding: 15px;

			&-info {
				column-gap: 20px;
				row-gap: 15px;
				grid-template-columns: 81px minmax(0, 120px);
			}

			&-advantage {
				grid-row: unset;
			}

			&-caption {
				grid-row: 1;
				grid-column: 1/-1;
			}

			&-tariff {
				&-description {
					span {
						display: flex;
						flex-direction: column;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.recommendations {
		margin-top: 30px;

		&_title {
			font-size: 16px;
			line-height: 19px;
		}

		&_card {
			grid-template-columns: 100%;
			align-items: flex-start;

			&-info {
				grid-template-columns: 81px auto;
			}

			&-price {
				flex-direction: row;
				flex-wrap: wrap;
				column-gap: 3px;
				margin-top: 15px;
				row-gap: 2px;

				&-month {
					font-size: 13px;
					line-height: 16px;
					font-weight: 500;
					color: #377C2B;
					order: 1;

					&::after {
						content: ', ';
					}
				}

				&-advantage {
					font-size: 13px;
					line-height: 16px;
					font-weight: 500;
					color: #377C2B;
					order: 2;
				}

				&-total {
					font-size: 24px;
					line-height: 28px;
					color: #000;
					display: block;
					width: 100%;
					order: 3;

					span:first-child {
						display: none;
					}
				}
			}
		}

		.button {
			margin-top: 15px;
			width: 100%;
			max-width: 100%;
		}
	}
}
