.chosen-tariff {
	width: 100%;
	margin-top: 50px;

	&_title {
		@include font(24px, 28px, #2A4045, 400);
		margin-bottom: 16px;
		font-family: 'Roboto', sans-serif;
	}

	&_card {
		width: 100%;
		background: $white;
		border: 2px solid rgba(141, 198, 63, 1);
		border-radius: 3px;
		position: relative;
		min-height: 202px;
		box-shadow: 0 5px 34px rgba(0, 0, 0, 0.1);
		display: flex;

		&-link {
			@include font(14px, 21px, #000, 400);
			padding: 36px;
			font-family: 'Roboto', sans-serif;
			text-decoration: none;
			display: grid;
			grid-template-columns: 139px minmax(0, 210px) minmax(0, 182px);
			column-gap: 16px;
			justify-content: space-between;
			width: 100%;
		}

		&-promo {
			@include font(13px, 15px, $white, 400);
			padding: 7px 25px;
			border-radius: 15px;
			background: rgba(255, 145, 0, 1);
			position: absolute;
			top: -14px;
			right: 36px;
		}

		&-head {
			grid-column: 3;
		}

		&-title {
			&-price {
				@include font(32px, 38px, #000, 400);
			}

			&-before {
				@include font(14px, 21px, rgba(0,0,0,0.5), 400);
				text-decoration-line: line-through;
				display: block;
			}

			&-name {
				@include font(16px, 25px, #377C2B, 500);
				display: none;

				&.desktop {
					display: block;
					margin-top: 10px;
				}
			}
		}

		&-image {
			display: none;
			max-width: 63px;

			&.desktop {
				display: flex;
			}

			svg {
				width: 100%;
				height: auto;
				fill: #8DC63F;
			}
		}

		&-feature {
			grid-row: 1 / -3;
			grid-column: 2;
			color: rgba(0,0,0,0.5);

			& > div:not(:first-child) {
				margin-top: 15px;
			}

			&-box {
				position: relative;
			}

			&-count {
				@include font(24px, 28px, #000, 400);

				&.show-tip {
					border-bottom-width: 1px;
					border-bottom-style: dashed;
					border-color: #b09090;
					cursor: pointer;
					width: fit-content;

					span {
						color: rgba(0,0,0,.5);
						font-size: 14px;
						line-height: 21px;
					}

					&:hover + .chosen-tariff_card-feature-tip {
						opacity: 1;
						transform: scale(1);
						transition: all 0.3s ease 0s;
					}
				}
			}

			&-tip {
				@include font(14px, 21px, rgba(0, 0, 0, 0.5), 400);
				margin: 0;
				opacity: 0;
				transform: scale(0);
				position: absolute;
				top: 100%;
				left: 0;
				background: #ffffff;
				padding: 35px;
				box-shadow: 0 5px 34px rgba(0, 0, 0, 0.1);
				border-radius: 3px;
				transform-origin: 0 -20px;
				transition: all 0.3s ease 0s;

				&:hover {
					opacity: 1;
					transform: scale(1);
					transition: all 0.3s ease 0s;
				}
			}
		}

		&-cinema {
			@include font(14px, 21px, #000, 400);
		}

		&-desktop-content {
			grid-column: 1;
			grid-row: 1 / -3;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}

		.button {
			grid-column: 3;
		}
	}
}

@media screen and (max-width: 1024px) {
	.chosen-tariff {
		&_card {
			&-desktop-content {
				display: none;
			}

			&-head {
				display: grid;
				width: 100%;
				min-height: 145px;
				border-bottom: 1px solid #8dc63f;
				position: relative;
			}

			&-feature {
				@include font(14px, 21px, rgba(0, 0, 0, 0.5), 400);
				min-height: 113px;
				margin-top: 17px;
				padding-bottom: 12px;

				& > div {
					margin-top: 5px;
				}

				&-count {
					@include font(20px, 23px, #000, 400);

					&-of {
						font-size: 14px;
						line-height: 21px;
					}
				}

				.padding-sm {
					padding-bottom: 14px;
				}
			}

			&-cinema {
				border-top: 1px solid rgba(151, 151, 151, 0.5);
				padding-top: 20px;

				&-text {
					color: rgba(0, 0, 0, 0.5);
				}
			}

			&-link {
				display: block;
				padding: 15px;

				.button {
					padding: 18px 72px;
				}
			}

			&-title {
				grid-area: 1/-1;
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				position: relative;
				padding-bottom: 12px;

				&-name {
					display: block;
					position: static;
				}
			}

			&-promo {
				display: inline-flex;
				flex-direction: column;
				position: static;
				border-radius: 3px;
				padding: 7px 17px 7px 16px;
			}

			&-image {
				grid-area: 1/-1;
				display: flex;
				align-items: flex-end;
				margin-bottom: -1px;
				position: static;
				top: unset;
				left: unset;
				width: 100%;
				max-width: 90px;
				justify-self: flex-end;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.chosen-tariff {
		margin-top: 30px;

		&_title {
			font-size: 16px;
			line-height: 19px;
		}

		.button {
			width: 100%;
			max-width: 100%;
		}
	}
}
