.choose-tariff
	width: 100%
	font-family: 'Roboto', sans-serif

	&_wrapper
		position: relative
		width: 100%

	&_buttons
		display: flex
		justify-content: center
		width: 100%
		margin-top: 30px
		text-align: center

	&_slider
		width: 100%
		margin-top: 33px
		position: static

	&_list
		width: 100%
		margin: 0
		list-style: none
		padding: 30px 0 25px

	&_item
		border: 1px solid rgba(151, 151, 151, 0.2)
		border-radius: 3px
		background: #ffffff
		width: 277.4px
		height: auto
		display: block
		overflow: hidden

		@media (hover: hover)
			transform: translateY(0)
			transition: all 0.3s ease 0s

		@media (hover: hover)
			&:hover
				box-shadow: 0 5px 30px rgba(0,0,0,.1)
				transform: translateY(-10px)
				transition: all 0.3s ease 0s

		&-green
			background: #377C2B

			.choose-tariff_item
				&-title
					&-name
						color: #8DC63F

					&-price
						color: #ffffff

				&-feature
					color: #C1C8C9

					&-count
						color: #ffffff

						&-of
							span
								color: #c1c8c9

				&-advantage
					color: #C1C8C9

				&-description
					color: #C1C8C9

		&-wrapper
			font-size: 14px
			line-height: 20px
			color: #000
			font-weight: 400
			display: flex
			flex-direction: column
			width: 100%
			height: 100%
			text-decoration: none
			padding: 15px

			.button
				width: 100%
				display: block
				margin-top: 20px
				flex: 0 0 auto

		&-promo
			font-size: 13px
			line-height: 15px
			color: #ffffff
			font-weight: 400
			display: inline-flex
			flex-direction: column
			letter-spacing: 0.5px
			background-color: #FF9100
			border-radius: 3px
			padding: 7px 17px 7px 16px
			position: absolute
			left: 15px
			top: 15px
			z-index: 1

		&-head
			border-bottom: 1px solid #8dc63f
			position: relative
			display: grid
			width: 100%
			min-height: 189px

			svg
				fill: #8DC63F
				display: block

		&-image
			grid-area: 1/-1
			display: flex
			justify-content: flex-end
			align-items: flex-end
			margin-bottom: -1px

			&-lg
				margin-top: -37px
				margin-right: -32px

			&-xl
				margin-top: -90px
				margin-right: -64px

		&-title
			grid-area: 1/-1
			display: flex
			flex-direction: column
			justify-content: flex-end
			position: relative
			padding-bottom: 12px

			&-name
				font-size: 16px
				line-height: 25px
				color: #377C2B
				font-weight: 500
				@include font(16px, 25px, #377C2B, 500)
				text-transform: uppercase

			&-price
				@include font(32px, 38px, #000, 400)

			&-before
				@include font(20px, 23px, rgba(0, 0, 0, 0.3), 400)
				text-decoration-line: line-through

		&-feature
			@include font(14px, 21px, rgba(0, 0, 0, 0.5), 400)
			border-bottom: 1px solid rgba(151, 151, 151, 1)
			min-height: 107px
			margin-top: 17px
			padding-bottom: 12px

			&-channel
				border-bottom-width: 1px
				border-bottom-style: dashed
				border-color: #b09090
				cursor: pointer
				width: fit-content

			.show-tip:hover + .choose-tariff_item-tip
				opacity: 1
				transform: scale(1)
				transition: all 0.3s ease 0s

			& > div
				margin-top: 5px

			&-count
				@include font(20px, 23px, #000, 400)

				&-of
					font-size: 20px
					line-height: 23px
					border-bottom-width: 1px
					border-bottom-style: dashed
					border-color: #b09090
					cursor: pointer
					width: fit-content

					span
						color: rgba(0,0,0,.5)
						font-size: 14px
						line-height: 21px

		&-advantage
			@include font(14px, 20px, rgba(0, 0, 0, 0.5), 400)
			border-bottom: 1px solid rgba(151, 151, 151, 1)
			margin-top: 19px
			min-height: 61px
			display: flex
			flex-direction: column
			padding-bottom: 12px


		&-description
			@include font(13px, 15px, rgba(0, 0, 0, 0.5), 400)
			letter-spacing: 0.5px
			margin-top: 20px
			flex: 1 0 auto

		&-cinema
			position: relative

			&-text
				@include font(18px, 21px, #6E6E6E, 300)
				text-transform: uppercase

		&-tip
			@include font(14px, 21px, rgba(0, 0, 0, 0.5), 400)
			margin: 0
			opacity: 0
			transform: scale(0)
			position: absolute
			top: 100%
			left: 0
			background: #ffffff
			padding: 35px
			box-shadow: 0 5px 34px rgba(0, 0, 0, 0.1)
			border-radius: 3px
			transform-origin: 0 -20px
			transition: all 0.3s ease 0s

			&:hover
				opacity: 1
				transform: scale(1)
				transition: all 0.3s ease 0s

	&_navigation
		&-controls
			&-button
				display: flex
				width: 45px
				height: 45px
				align-items: center
				justify-content: center
				border: 0
				border-radius: 50%
				background: #31494E
				opacity: 0.6
				position: absolute
				top: 50%
				transform: translateY(-50%)
				z-index: 10
				transition: all 0.3s ease 0s

				&.swiper-button-disabled
					opacity: 0.2
					cursor: default
					transition: all 0.3s ease 0s

				&-prev
					left: -35px

				&-next
					right: -35px

				svg
					fill: none
					stroke-width: 2px
					stroke-linecap: round
					stroke: #ffffff

		&-title
			display: none

		&-icon
			display: flex
			align-items: center
			justify-content: center

			&-mobile
				display: none

	&_pagination
		display: none

@media screen and (max-width: 1280px)
	.choose-tariff
		&_navigation
			display: flex
			justify-content: space-between

			&-icon
				display: none

				&-mobile
					display: block

			&-title
				@include font(14px, 16px, #2A4045, 400)
				display: block

			&-controls
				display: flex
				column-gap: 20px
				align-items: center

				&-button
					display: flex
					align-items: center
					position: static
					width: unset
					height: unset
					background: 0
					border: 0
					border-radius: 0
					transform: unset

					&.swiper-button-disabled
						cursor: default

						svg
							opacity: 0.2

					svg
						opacity: 0.4
						fill: #000
						stroke: #000
						stroke-width: 2px
						stroke-linecap: square

		&_pagination
			@include font(12px, 21px, rgba(0,0,0,0.5), 400)
			display: inline

@media screen and (max-width: 768px)
	.choose-tariff
		&_slider
			margin-top: 16px

		&_list
			padding-top: 23px

		&_item
			&-title
				&-price
					font-size: 24px
					line-height: 28px

			.button
				margin-top: 15px

			&-feature
				min-height: 99px
				padding-bottom: 15px
				margin-top: 15px
				font-size: 13px
				line-height: 21px

				&-count
					font-size: 16px
					line-height: 19px

			&-advantage
				padding-bottom: 15px
				margin-top: 15px
				font-size: 13px
				line-height: 17px

			&-description
				margin-top: 15px
				font-size: 13px
				line-height: 15px
