.choice-equipment {
	width: 100%;
	margin-top: 50px;

	&_title {
		@include font(24px, 28px, #2A4045, 400);
		margin-bottom: 16px;
		font-family: 'Roboto', sans-serif;
	}

	&_slider {
		width: 100%;
		margin-top: 20px;
	}

	&_list {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	&_item {
		width: 100%;
		max-width: 230px;
		border: 1px solid rgba(151, 151, 151, 0.2);
		border-radius: 3px;
		background: $white;
		padding: 20px;
		height: auto;
		display: flex;
		flex-direction: column;
		overflow-y: hidden;

		&.active {
			background: #80b636;
			transition: all 0.3s ease;

			.button_theme_full-green {
				background: $white;
				color: #000;
				transition: all 0.3s ease;
			}

			.choice-equipment_item-button.active {
				background: $white;
				color: #000;
				transition: all 0.3s ease;

				span {
					&:before {
						background: $white;
						transition: all 0.3s ease;
					}
				}

				&:before,
				&:after {
					background: #80b636;
					transition: all 0.3s ease;
				}
			}
		}

		&-image {
			width: 100%;
			height: 151px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: $white;

			img {
				width: unset;
				object-fit: cover;
				max-width: 100%;
			}
		}

		&-name {
			@include font(13px, 25px, #000, 400);
			margin-top: 2px;
			display: block;
			text-align: center;
		}

		&-model {
			@include font(16px, 19px, #000, 700);
			letter-spacing: 0.5px;
			display: block;
			text-align: center;
		}

		&-descriptions {
			@include font(14px, 17px, #000, 400);
			display: flex;
			flex-direction: column;
			row-gap: 10px;
			margin-top: 30px;
			letter-spacing: 0.55px;
			flex: 1 0 auto;

			span.nowrap {
				white-space: nowrap;
				flex-shrink: 0;
			}
		}

		&-description {
			display: flex;
			justify-content: space-between;
			column-gap: 8px;
		}

		&-buttons {
			display: flex;
			align-items: flex-end;
			justify-content: center;
			column-gap: 12px;
			margin-top: 30px;
			padding-bottom: 7px;
			flex: 1;
		}

		&-button {
			@include font(14px, 16px, #000, 400);
			background: rgba(0, 0, 0, 0.1);
			flex: 0 0 auto;
			width: 90px;
			height: 34px;
			border-radius: 42px;
			text-align: center;
			display: block;
			position: relative;
			z-index: 1;

			&.active {
				color: $white;
				background: #8DC63F;
				cursor: default;
				user-select: unset;

				span {
					position: relative;
					display: block;
					z-index: 0;

					&::before {
						content: '';
						top: calc(100% + 8px);
						left: 50%;
						transform: translateX(-50%);
						background: #8dc63f;
						width: 22px;
						height: 7px;
						position: absolute;
						display: block;
					}
				}

				&::before {
					content: '';
					width: 50%;
					height: 7px;
					background: $white;
					position: absolute;
					left: 0;
					top: 100%;
					z-index: 1;
					border-top-right-radius: 10px;
				}

				&::after {
					content: '';
					width: 50%;
					height: 7px;
					background: $white;
					position: absolute;
					right: 0;
					top: 100%;
					z-index: 1;
					border-top-left-radius: 10px;
				}
			}
		}

		&-prices {
			display: grid;
			grid-template-columns: 100%;
			min-height: 63px;
		}

		&-price {
			@include font(32px, 38px, #000, 400);
			grid-area: 1 /-2;
			display: block;
			opacity: 0;
			text-align: center;
			z-index: -1;
			transform: translateX(-100%);
			transition: all 0.5s ease 0s;
			margin-top: 25px;

			&.active {
				opacity: 1;
				position: static;
				z-index: 0;
				transform: translateX(0%);
				transition: all 0.5s ease 0s;
			}
		}

		&-add-button {
			margin-top: 26px;
		}

		&.active {
			.choice-equipment_item-add-button {
				background: #ffffff;
				transition: all 0.3s ease;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.choice-equipment {
		margin-top: 30px;

		&_title {
			font-size: 16px;
			line-height: 19px;
		}

		&_item {
			max-width: 230px;
			padding: 15px;

			&-description {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				row-gap: 3px;

				span {
					font-size: 13px;
					line-height: 15px;
				}
			}

			&-term {
				font-size: 	12px;
				line-height: 14px;
				color: rgba(0, 0, 0, 0.5);
			}

			&-buttons {
				margin-top: 20px;
				column-gap: 5px;
			}

			&-button {
				width: 98px;
				font-size: 13px;
				line-height: 15px;
			}

			&-price {
				margin-top: 20px;
				font-size: 24px;
				line-height: 28px;
			}
		}
	}
}
