.request {
	width: 100%;

	&_wrapper {
		width: 100%;
		display: grid;
		grid-template-columns: minmax(631px, 737px) minmax(320px, 431px);
		column-gap: 30px;
		align-items: flex-start;
		justify-content: space-between;
	}

	&_aside {
		background: #C1C8C9;
		position: sticky;
		top: 95px;
		right: 0;
		border-radius: 3px;
		z-index: 10;

		&-title {
			@include font(24px, 28px, #2A4045, 400);
			font-family: 'Roboto', sans-serif;
			padding: 40px 30px;
			border-bottom: 1px solid rgba(0, 0, 0, 0.1);

			svg {
				display: none;
			}

			&-mobile {
				display: none;
			}
		}
	}

	&_description {
		padding: 22px 30px 20px 30px;
		border-top: 1px solid rgba(0, 0, 0, 0.1);
		font-family: 'Montserrat', sans-serif;
		display: flex;
		flex-direction: column;
		row-gap: 16px;

		&-price {
			@include font(16px, 20px, #000000, 700);
			display: flex;
			justify-content: space-between;
			column-gap: 8px;

			span:last-child {
				flex-shrink: 0;
			}
		}

		&-tariff {
			@include font(16px, 20px, #474747, 400);
			display: flex;
			justify-content: space-between;
			column-gap: 8px;

			span:last-child {
				flex-shrink: 0;
			}
		}

		&-total {
			&-prices {
				display: flex;
				flex-direction: column;
				row-gap: 20px;
				padding: 20px 35px 40px 20px;
			}

			&-price {
				@include font(18px, 22px, #000, 700);
				font-family: 'Montserrat', sans-serif;
				display: flex;
				justify-content: space-between;
				column-gap: 8px;

				span:last-child {
					flex-shrink: 0;
				}
			}
		}

		&-bonus {
			display: none;

			span:last-child {
				flex-shrink: 0;
			}

			&.show {
				@include font(16px, 20px, #377C2B, 500);
				font-family: 'Montserrat', sans-serif;
				display: flex;
				justify-content: space-between;
				column-gap: 8px;
			}
		}

		&-close-button {
			display: none;
		}
	}
}

@media screen and (max-width: 1024px) {
	.request {
		&_wrapper {
			grid-template-columns: 100%;
		}

		&_aside {
			position: fixed;
			top: unset;
			bottom: 0;
			left: 0;
			width: 100%;
			overflow: auto;
			border-radius: 10px 10px 0 0;
			transform: translateY(100%);
			transition: all 0.3s ease 0s;

			&.scroll {
				transform: translateY(0%);
				transition: all 0.3s ease 0s;
			}

			&-title {
				display: flex;
				align-items: center;
				cursor: pointer;
				border: 0;

				&.show {
					svg {
						transform: rotate(-180deg);
						transition: all 0.3s ease 0s;
					}
				}

				span {
					width: 100%;
				}

				svg {
					display: inline-block;
					vertical-align: middle;
					stroke-width: 2px;
					stroke-linecap: round;
					stroke: #31494e;
					fill: none;
					transform: rotate(0deg);
					transition: all 0.3s ease 0s;
				}

				&-mobile {
					display: inline;
				}
			}
		}

		&_descriptions {
			height: 0;
			overflow: hidden;
			transition: all 0.3s ease 0s;
		}

		&_description {
			padding: 0;
			position: relative;

			& div {
				padding: 0 30px;

				&:first-child {
					padding-top: 22px;
				}

				&:last-child {
					padding-bottom: 20px;
				}
			}

			&-total-prices {
				display: none;
			}

			&-close-button {
				width: 100%;
				display: flex;
				justify-content: center;

				button {
					background: 0;
					border: 0;
					cursor: pointer;
					width: 30px;
					height: 30px;
					display: flex;
					align-items: center;
					justify-content: center;
				}

				svg {
					display: inline-block;
					vertical-align: middle;
					stroke-width: 2px;
					stroke-linecap: round;
					stroke: #31494e;
					fill: none;
					transform: rotate(0deg);
					transition: all 0.3s ease 0s;
				}
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.request {
		&_aside {
			&-title {
				padding: 20px;
				font-size: 16px;
				line-height: 19px;
				font-weight: 600;
				color: #474747;
			}
		}

		&_description {
			& div {
				padding: 0 20px;

				&:first-child {
					padding-top: 18px;
				}

				&:last-child {
					padding-bottom: 18px;
				}
			}

			&-tariff {
				font-size: 13px;
				line-height: 16px;
				color: #474747;
			}

			&-price {
				font-size: 13px;
				line-height: 15px;
				font-weight: 500;
				color: #31494E;
			}
		}
	}
}
