.scrollable-buttons {
	display: flex;
	align-items: center;
	-ms-overflow-style: none;
	scrollbar-width: none;
	column-gap: 20px;

	&::-webkit-scrollbar {
		display: none;
	}

	&_button {
		@include font(16px, 19px, #000, 400);
		border: 1px solid #979797;
		border-radius: 27px;
		padding: 14px 44px;
		background: 0;
		flex-shrink: 0;
		transition: all 0.3s ease 0s;

		&:hover {
			box-shadow: 0 0 20px 0 #8DC63F;
			transition: all 0.3s ease 0s;
		}

		&.active {
			background-color: #8DC63F;
			border-color: #8DC63F;
			color: $white;
		}
	}
}

@media screen and (max-width: 768px) {
	.scrollable-buttons {
		overflow-x: scroll;

		&_button {
			font-size: 14px;
			line-height: 16px;
			padding: 14px 30px;

			&:hover {
				box-shadow: unset;
			}
		}
	}
}
