.additionally {
	width: 100%;
	margin-top: 50px;

	&_title {
		@include font(24px, 28px, #2A4045, 400);
		margin-bottom: 16px;
		font-family: 'Roboto', sans-serif;
	}

	&_wrapper {
		width: 100%;
		position: relative;
	}

	&_slider {
		width: 	100%;
	}

	&_list {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	&_item {
		width: 100%;
		max-width: 230px;
		height: auto;
		text-align: center;
		display: flex;
		flex-direction: column;
		padding: 30px 45px;
		background: $white;
		border-radius: 3px;
		transition: background 0.3s ease 0s;

		&.active {
			background: #8DC63F;
			transition: background 0.3s ease 0s;

			.additionally_item {
				&-title {
					color: $white;
				}

				&-channel {
					color: $white;
				}

				&-price {
					color: $white;
				}

				&-button {
					background: #ffffff;
					color: #8DC63F;
				}
			}
		}

		&-image {
			border-radius: 3px;
			display: block;
			width: 100%;
			flex: 1;

			img {
				border-radius: inherit;
			}
		}

		&-title {
			@include font(16px, 25px, #000000, 700);
			margin-top: 12px;
			flex: 1;
		}

		&-channel {
			@include font(14px, 25px, #6E6E6E, 400);
			text-decoration: underline;
			text-decoration-style: dashed;
			cursor: pointer;
			padding: 0 5px;
			flex: 1;
		}

		&-price {
			@include font(24px, 25px, #6E6E6E, 400);
			flex: 1;
		}

		&-button {
			margin-top: 23px;
			flex: 0;

			&.button_size_medium {
				padding-right: 8px;
				padding-left: 8px;
			}
		}
	}

	&_button {
		display: flex;
		width: 45px;
		height: 45px;
		align-items: center;
		justify-content: center;
		border: 0;
		border-radius: 50%;
		background: #31494E;
		opacity: 0.6;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 10;
		transition: all 0.3s ease 0s;

		&.swiper-button-disabled {
			opacity: 0.2;
			cursor: default;
			transition: all 0.3s ease 0s;
		}

		&-prev {
			left: -20px;
		}

		&-next {
			right: -20px;
		}

		svg {
			fill: none;
			stroke-width: 2px;
			stroke-linecap: round;
			stroke: $white;
		}
	}
}

@media screen and (max-width: 1024px) {
	.additionally {
		&_button {
			display: none;
		}
	}
}

@media screen and (max-width: 576px) {
	.additionally {
		margin-top: 30px;

		&_title {
			font-size: 16px;
			line-height: 19px;
		}

		&_item {
			padding: 25px 10px 10px 10px;
			justify-content: center;
			align-items: center;
		}
	}
}
